e":"sweetalert2.scss","sourcesContent":[".swal2-popup {\n  &.swal2-toast {\n    flex-direction: row;\n    align-items: center;\n    width: $swal2-toast-width;\n    padding: $swal2-toast-padding;\n    overflow-y: hidden;\n    background: $swal2-toast-background;\n    box-shadow: $swal2-toast-box-shadow;\n\n    .swal2-header {\n      flex-direction: row;\n      padding: $swal2-toast-header-padding;\n    }\n\n    .swal2-title {\n      flex-grow: 1;\n      justify-content: flex-start;\n      margin: $swal2-toast-title-margin;\n      font-size: $swal2-toast-title-font-size;\n    }\n\n    .swal2-footer {\n      margin: $swal2-toast-footer-margin;\n      padding: $swal2-toast-footer-margin;\n      font-size: $swal2-toast-footer-font-size;\n    }\n\n    .swal2-close {\n      position: static;\n      width: $swal2-toast-close-button-width;\n      height: $swal2-toast-close-button-height;\n      line-height: $swal2-toast-close-button-line-height;\n    }\n\n    .swal2-content {\n      justify-content: flex-start;\n      padding: $swal2-toast-content-padding;\n      font-size: $swal2-toast-content-font-size;\n    }\n\n    .swal2-icon {\n      width: 2em;\n      min-width: 2em;\n      height: 2em;\n      margin: 0;\n\n      .swal2-icon-content {\n        display: flex;\n        align-items: center;\n        font-size: 1.8em;\n        font-weight: bold;\n\n        @include ie {\n          font-size: .25em;\n        }\n      }\n\n      &.swal2-success {\n        .swal2-success-ring {\n          width: 2em;\n          height: 2em;\n        }\n      }\n\n      &.swal2-error {\n        [class^='swal2-x-mark-line'] {\n          top: .875em;\n          width: 1.375em;\n\n          &[class$='left'] {\n            left: .3125em;\n          }\n\n          &[class$='right'] {\n            right: .3125em;\n          }\n        }\n      }\n    }\n\n    .swal2-actions {\n      flex-basis: auto !important;\n      width: auto;\n      height: auto;\n      margin: 0 .3125em;\n    }\n\n    .swal2-